body {
  overflow-x: hidden;
}

/* Style all */
.h5 {
  font-size: 20px;
  font-weight: 500;
  color: #212529;
  margin-bottom: 2px;
}

.btn {
  display: inline-block;
  padding: var(--btn-padding);
  font-weight: 500;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: all 300ms ease;
  font-size: 0.9rem;
}

.btn:hover {
  opacity: 0.8;
}

.btn-primary {
  background: var(--color-orange);
  color: var(--color-white);
}

.text-bold {
  font-weight: 500;
}

/*================== MAIN ==================*/
main {
  position: relative;
  top: 5.4rem;
}

main .container {
  display: grid;
  grid-template-columns: 18vw auto 20vw;
  column-gap: 2rem;
  position: relative;
}

/*================== LEFT ==================*/

main .container .left {
  height: max-content;
  position: sticky;
  top: var(--sticky-top-left);
}

main .container .left .profile {
  padding: var(--card-padding);
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  display: flex;
  align-items: center;
  column-gap: 1.2rem;
  width: 100%;
  height: max-content;
}

main .container .left .profile h4 {
  font-weight: 700;
}

main .container .left .profile .profile-photo {
  width: 2rem;
  height: 2rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}

main .container .left .profile .profile-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.middle .create-post .profile-photo-post {
  width: 55px;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}

.middle .create-post .profile-photo-post img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

main .container .left a {
  display: block;
  transition: all 300ms ease;
  text-decoration: none !important;
  color: inherit;
}

/*================== SIDEBAR ==================*/

.left .sidebar {
  margin-top: 1rem;
  background: var(--color-white);
  border-radius: var(--card-border-radius);
}

.left .sidebar a {
  transition: all 300ms ease;
  text-decoration: none !important;
  color: inherit;
}

.left .sidebar .menu-item {
  display: flex;
  align-items: center;
  height: 4rem;
  cursor: pointer;
  transition: all 300ms ease;
  position: relative;
}

.left .sidebar .menu-item:hover {
  background: var(--color-light);
}

.left .sidebar i {
  font-size: 1.4rem;
  color: var(--color-gray);
  margin-left: 2rem;
  position: relative;
}

.left .sidebar i .notification-count {
  background: var(--color-danger);
  color: white;
  font-size: 0.7rem;
  width: fit-content;
  border-radius: 0.8rem;
  padding: 0.1rem 0.4rem;
  position: absolute;
  top: -0.2rem;
  right: -0.3rem;
}

.left .sidebar h3 {
  margin-left: 1.5rem;
  font-size: 1rem;
  font-weight: 700;
}

.left .sidebar .active {
  background: var(--color-light);
}

.left .sidebar .active i,
.left .sidebar .active h3 {
  color: var(--color-orange);
}

.left .sidebar .active::before {
  content: "";
  display: block;
  width: 0.5rem;
  height: 100%;
  position: absolute;
  background: var(--color-orange);
}

.left .sidebar .menu-item:only-child {
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  /* overflow: hidden; */
}

.left .sidebar .menu-item:first-child.active {
  background: var(--color-white);
  border-top-left-radius: var(--card-border-radius);
  border-top-right-radius: var(--card-border-radius);
  overflow: hidden;
}

.left .sidebar .menu-item:last-child.active {
  background: var(--color-white);
  border-bottom-right-radius: var(--card-border-radius);
  border-bottom-left-radius: var(--card-border-radius);
  overflow: hidden;
}

.left .btn {
  margin-top: 1rem;
  width: 100%;
  text-align: center;
  padding: 1rem 0;
}

/*================== NOFITICATION POPUP ==================*/
.left .notifications-popup {
  position: absolute;
  top: 0;
  left: 110%;
  width: 30rem;
  height: 257px;
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  border: 1px solid var(--color-light);
  box-shadow: 0 0 2rem hsl(var(--color-orange), 75%, 60% 25%);
  z-index: 8;
  // display: none;
  overflow-y: auto;
}

.left .notifications-popup::-webkit-scrollbar {
  width: 0.5rem;
}

.left .notifications-popup::before {
  content: "";
  width: 1.2rem;
  height: 1.2rem;
  display: block;
  background: var(--color-white);
  position: absolute;
  left: -0.6rem;
  transform: rotate(45deg);
}

.left .notifications-popup > div {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1rem;
}

.left .notifications-popup small {
  display: block;
}

.left .notifications-popup b {
  font-weight: 600;
}

/*================== NOFITICATION DELINE ==================*/

.left .notifications-deline {
  position: absolute;
  top: 0;
  left: 110%;
  width: 30rem;
  height: 257px;
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  border: 1px solid var(--color-light);
  box-shadow: 0 0 2rem hsl(var(--color-orange), 75%, 60% 25%);
  z-index: 8;
  // display: none;
  overflow-y: auto;
}

.left .notifications-deline::-webkit-scrollbar {
  width: 0.5rem;
}

.left .notifications-deline::before {
  content: "";
  width: 1.2rem;
  height: 1.2rem;
  display: block;
  background: var(--color-white);
  position: absolute;
  left: -0.6rem;
  transform: rotate(45deg);
}

.left .notifications-deline > div {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1rem;
}

.left .notifications-deline small {
  display: block;
}

.left .notifications-deline b {
  font-weight: 600;
}

/*================== CREATE POST ==================*/
.middle .create-post {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
}

.middle .create-post .profile-photo {
  width: 3.7rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}

.middle .create-post .profile-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.middle .create-post input[type="text"] {
  justify-self: start;
  width: 100%;
  padding-left: 1rem;
  background: transparent;
  color: var(--color-dark);
  margin-right: 1rem;
}

/*================== FEEDs ==================*/
.middle .feeds .feed {
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  margin: 1rem 0;
  font-size: 0.85rem;
  line-height: 1.5;
}

.middle .feed .head {
  display: flex;
  justify-content: space-between;
}

.middle .feed .user {
  display: flex;
  gap: 1rem;
}

.middle .feed .user h3 {
  font-weight: 700;
}

.middle .feed .photo {
  border-radius: var(--card-border-radius);
  overflow: hidden;
  margin: 0.7rem 0;
}

.middle .feed .action-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.4rem;
  margin: 0.6rem;
}

.middle .feed .action-buttons .interaction-buttons {
  display: flex;
  gap: 0.5rem;
}

.middle .feed .action-buttons .interaction-buttons span button {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.middle .feed .action-buttons .custom-action {
  display: flex;
  gap: 0.2rem;
  align-items: center;
  padding-right: 5px;
}

.middle .liked-by {
  display: flex;
}

.middle .liked-by span {
  width: 1.4rem;
  height: 1.4rem;
  display: block;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--color-white);
  margin-left: -0.6rem;
}

.middle .liked-by span:first-child {
  margin: 0;
}

.middle .liked-by p {
  margin-left: 0.5rem;
}

.middle .liked-by b {
  font-weight: 600;
}

.middle .caption b {
  font-weight: 600;
}

.middle .cmt {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0.5rem 0;
  background-color: var(--color-light);
  border-radius: var(--border-radius);
}

.middle .cmt .create-cmt {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  margin: 0.5rem 0;
}

.middle .cmt .create-cmt .profile-photo-cmt {
  padding-left: 10px;
}

.middle .cmt .create-cmt input {
  width: 70%;
  padding-left: 20px;
  background-color: var(--color-light);
  color: var(--color-gray);
}

.middle .cmt .create-cmt button {
  width: 21%;
  padding: 0.5rem;
}

.middle .comments {
  padding-left: 10px;
  width: 100%;
  height: max-content;
}

.middle .comments .profile-cmt {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 0.5rem;
}

.middle .profile-photo-cmt img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.middle .handle-cmt h4 {
  font-weight: 600;
}

/*================== RIGHT ==================*/
main .container .right {
  height: max-content;
  position: sticky;
  top: var(--sticky-top-right);
  bottom: 0;
}

.right .messages {
  height: 500px;
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  overflow-y: auto;
}

.right .messages::-webkit-scrollbar {
  width: 0.5rem;
}

.right .messages .heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.right .messages .heading h4 {
  font-weight: 700;
}

.right .messages i {
  font-size: 1.4rem;
}

.right .messages .search-bar {
  display: flex;
  margin-bottom: 1rem;
}

.right .messages .category {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.right .messages .category h6 {
  width: 100%;
  text-align: center;
  border-bottom: 4px solid var(--color-light);
  padding-bottom: 0.5rem;
  font-size: 0.85rem;
  font-weight: 600;
}

.right .messages .category .active {
  border-color: var(--color-dark);
}

.right .messages .message-requests {
  color: var(--color-orange);
}

.right .messages .message {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  align-items: flex-start;
}

.right .messages .message:last-child {
  margin: 0;
}

.right .messages .message p {
  font-size: 0.8rem;
}

.right .messages .message .profile-photo {
  position: relative;
  overflow: visible;
}

.right .messages .message .profile-photo img {
  border-radius: 50%;
}

.right .messages .message .profile-photo .active {
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 50%;
  border: 3px solid var(--color-white);
  background: var(--color-success);
  position: absolute;
  bottom: 0;
  right: 0;
}

.right .messages .message .message-body h5 {
  font-weight: 600;
}

/*================== FRIEND REQUESTs ==================*/

.right .friend-requests {
  margin-top: 1rem;
}

.right .friend-requests h4 {
  color: var(--color-gray);
  margin: 1rem 0;
}

.right .request {
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  margin-bottom: 0.7rem;
}

.right .request .info {
  display: flex;
  gap: 1rem;
  align-items: center;
  margin-bottom: 1rem;
}

.right .request .info h5 {
  font-weight: 700;
}

.right .request .action {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

/*================== Custom theme ==================*/

.customize-theme {
  background: rgba(255, 255, 255, 0.5);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  text-align: center;
  display: grid;
  place-items: center;
  font-size: 0.9rem;
  // display: none;
}

.customize-theme .card {
  background-color: var(--color-white);
  padding: 3rem;
  border-radius: var(--card-border-radius);
  width: 50%;
  box-shadow: 0 0 1rem var(--color-orange);
}

/*================== Font size ==================*/

.customize-theme .font-size {
  margin-top: 5rem;
}

.customize-theme .font-size > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--color-light);
  padding: var(--search-padding);
  border-radius: var(--card-border-radius);
}

.customize-theme .choose-size {
  background: var(--color-secondary);
  height: 0.3rem;
  width: 100%;
  margin: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.customize-theme .choose-size span {
  width: 1rem;
  height: 1rem;
  background: var(--color-secondary);
  border-radius: 50%;
  cursor: pointer;
}

.customize-theme .choose-size span.active {
  background: var(--color-orange);
}

/*================== Color ==================*/

.customize-theme .color {
  margin-top: 2rem;
}

.customize-theme .choose-color {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--color-light);
  padding: var(--search-padding);
  border-radius: var(--card-border-radius);
}

.customize-theme .choose-color span {
  width: 2.2rem;
  height: 2.2rem;
  border-radius: 50%;
}

.customize-theme .choose-color span:nth-child(1) {
  background: hsl(252, 75%, 60%);
}

.customize-theme .choose-color span:nth-child(2) {
  background: hsl(52, 75%, 60%);
}

.customize-theme .choose-color span:nth-child(3) {
  background: hsl(13, 91%, 54%);
}

.customize-theme .choose-color span:nth-child(4) {
  background: hsl(152, 75%, 60%);
}

.customize-theme .choose-color span:nth-child(5) {
  background: hsl(202, 75%, 60%);
}

.customize-theme .choose-color span.active {
  border: 5px solid white;
}

/*================== Background ==================*/

.customize-theme .background {
  margin-top: 2rem;
}

.customize-theme .choose-bg {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
}

.customize-theme .choose-bg > div {
  padding: var(--card-padding);
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 0.4rem;
  cursor: pointer;
}

.customize-theme .choose-bg > div.active {
  border: 2px solid var(--color-orange);
}

.customize-theme .choose-bg .bg-1 {
  background: white;
  color: black;
}

.customize-theme .choose-bg .bg-2 {
  background: hsl(252, 30%, 17%);
  color: white;
}

.customize-theme .choose-bg .bg-3 {
  background: hsl(252, 30%, 10%);
  color: white;
}

.customize-theme .choose-bg > div span {
  width: 2rem;
  height: 2rem;
  border: 2px solid var(--color-gray);
  border-radius: 50%;
  margin-right: 1rem;
}

/*================== responsive ==================*/

@media screen and (max-width: 1200px) {
  .container {
    width: 96%;
  }

  main .container {
    grid-template-columns: 5rem auto 30vw;
    gap: 1rem;
  }

  .left {
    width: 5rem;
    z-index: 5;
  }

  main .container .left .profile {
    display: none;
  }

  .sidebar h3 {
    display: none;
  }

  .left .btn {
    display: none;
  }

  .customize-theme .card {
    width: 80vw;
  }
}

@media screen and (max-width: 992px) {
  // nav .search-bar {
  //   display: none;
  // }

  main .container {
    grid-template-columns: 0 auto 5rem;
    gap: 0;
  }

  main .container .left {
    grid-column: 3/4;
    position: fixed;
    bottom: 0;
    right: 0;
  }

  .left .notifications-popup {
    position: absolute;
    left: -20rem;
    width: 20rem;
  }

  .left .notifications-popup::before {
    display: absolute;
    top: 1.3rem;
    left: calc(20rem - 0.6rem);
    display: block;
  }

  .left .notifications-deline {
    position: absolute;
    left: -20rem;
    width: 20rem;
  }

  .left .notifications-deline::before {
    display: absolute;
    top: 1.3rem;
    left: calc(20rem - 0.6rem);
    display: block;
  }

  main .container .middle {
    grid-column: 1/3;
  }

  main .container .right {
    display: none;
  }

  .customize-theme .card {
    width: 80vw;
  }
}

/* create modal post */
.modal-create-post {
  background: rgba(255, 255, 255, 0.5);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  display: grid;
  place-items: center;
  font-size: 0.9rem;
  // display: none;
}

.modal-create-post .cart-create {
  background-color: var(--color-white);
  padding: 2rem;
  border-radius: var(--card-border-radius);
  width: 40%;
  box-shadow: 0 0 1rem var(--color-gray);
}

.modal-create-post .cart-create h3 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 1rem;
  text-align: center;
}

.modal-create-post .form-create .form-input .status {
  margin-top: 10px;
}

.modal-create-post .form-create .form-input .status > div {
  display: inline;
  position: relative;
  cursor: pointer;
  padding-left: 10px;
  padding-right: 20px;
}

.modal-create-post .form-create .form-input .status input {
  width: 17px;
  height: 17px;
}

.modal-create-post .form-create .form-input .status span {
  font-size: 17px;
}

.modal-create-post .form-create button {
  width: 100%;
  height: 40px;
  font-size: 16px;
  font-weight: 600;
  background: #ff7225;
  border: 1px solid #e2e8f0;
  color: white;
  border-radius: 10px;
}

.modal-create-post .form-create .form-input {
  display: flex;
  flex-direction: column;
  padding: 15px;
  border-color: rgba(50, 151, 211, 0.25);
}

.modal-create-post .form-create .form-input label {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 5px;
}

.modal-create-post .form-create .form-input input[type="file"] {
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 5px;
  box-shadow: 0 1px 3px rgb(50 50 93 / 15%), 0 1px 0 rgb(0 0 0 / 2%);
}

.modal-create-post .form-create .form-input textarea {
  height: 100px;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 1px 3px rgb(50 50 93 / 15%), 0 1px 0 rgb(0 0 0 / 2%);
}

.modal-create-post .form-create .modal-profile {
  padding: var(--card-padding);
  display: flex;
  align-items: center;
  column-gap: 0.7rem;
  width: 100%;
}

.modal-create-post .form-create .modal-profile .handle h4 {
  font-weight: 600;
  font-size: 20px;
}
