.login{width:100%;height:100vh;display:flex;align-items:center;justify-content:center}.login .form{max-width:450px;width:100%;padding:30px;border-radius:12px;background:#fff}.login .form label{font-size:22px;font-weight:600;color:var(--color-black)}.login .form header{width:200px;margin:0 auto;margin-bottom:30px}.login .form header img{width:100%;display:flex}.login .form .field{position:relative;height:50px;width:100%;margin-top:20px;border-radius:6px}.login .form .link-field{height:20px;width:100%;margin-top:20px;text-align:right}.login .form .link-field a{font-size:15px;font-weight:600;text-decoration:none;color:var(--color-black)}.login .form input,.login .form button{height:100%;width:100%;border:none;font-size:16px;font-weight:600;border-radius:6px}.login .form .field input{padding:0 15px;border:1px solid gray}.login .form .field input:focus{border:1px solid #fc4a1a}.login .form .field input:hover{border:1px solid #fc4a1a}.login .form .eye-icon{position:absolute;top:50%;right:10px;transform:translateY(-50%);font-size:18px;color:#8b8b8b;cursor:pointer;padding:5px}.login .form .field button{color:#fff;background-color:#fc4a1a;transition:all .3s ease;cursor:pointer}.login .form .field button:hover{transition:.5s;background-color:#fd3721}.login .form .line{position:relative;height:1px;width:100%;margin:36px 0;background-color:gray}.login .form .line::before{content:"Hoc";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);background-color:#fff;color:gray;padding:0 15px}.login .form .media-options a{display:flex;align-items:center;justify-content:center}.login .form .media-options a:hover{background-color:#edebeb}.login .form img.google-img{position:absolute;top:50%;left:15px;transform:translateY(-50%)}.login .form img.google-img{height:20px;width:20px;object-fit:cover}.login .form a.google{border:1px solid gray}.login .form a.google span{font-weight:500;color:gray}